import React from 'react'
import '../Assets/css/ejepar.css'
import logo from '../Assets/images/EJE + Text.png'
import Paper from '@material-ui/core/Paper';
import Slide from 'react-reveal/Slide';
const EJEpar = () => {
    return (
        <Slide left>
        <div className="EJE_par" >
            <Paper  className="paper" elevation={3}>
            <div className="EJE_par_title">
                C'est quoi l'EJE ?
            </div>
            <div className="EJE_par_description">
                <img src={logo} className="EJE_par_logo" />
                <div className="EJE_par_description_left">

                Fonde en 2006, lENSI Junior Entreprise est une association tudiante, inscrite au journal officiel de la rpublique Tunisienne depuis 2011 et affile  lcole nationale des sciences de linformatique, ayant pour finalit linitiation des tudiants tunisiens  la vie professionnelle. Et ce  travers l laboration des activits bases sur trois axes principaux : Les formations, les vnements et la ralisation des projets dans le domaine des TIC. Salignant avec le concept de base du mouvement Junior-Entreprise, notre mission principale est de raliser des projets  plus-value pdagogique et conomique dans le domaine des technologies de linformation et de la communication TIC.
                                </div>
                <div className="EJE_par_description_right">
                En outre, nous nous dotons, videmment, de la responsabilit de promouvoir lesprit entrepreneurial dans lcosystme Tunisien  travers nos activits notamment nos vnements qui fortifient le lien tudiant-entreprise donnant ainsi aux tudiants le got dentreprendre et lenvie de sinvestir dans un projet qui leur est propre. Ensemble, avec le soutien de nos partenaires, nous sommes en mesure de progresser ,d'avancer et de russir  prserver les 16 ans dexcellence qui a toujours reprsent ltat desprit et le destin de cette entit.

                
                </div>
            </div>
            </Paper> 
        </div>
        </Slide>
    )
}

export default EJEpar
